@media screen and (min-width: 1025px) {
  html, body {
    overflow: hidden; } }

#app {
  height: 100%; }

.view {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
  max-height: 100%; }
  .view .view-content {
    overflow-y: auto; }
    .view .view-content.no-overflow {
      overflow-y: hidden; }

a[href] {
  color: #3F51B5; }
  a[href]:hover {
    color: #3949AB; }
